import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

const Categories = () => {
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark(filter: {frontmatter: {blueprint: {eq: "blog"}}}) {
        edges {
          node {
            frontmatter {
                blog_category
            }
          }
        }
      }
    }
  `);

  let categories = [];

 

  return (
    <div className="widget">
      <h3 className="widget-title">Categories</h3>
      <div className="widget-body">
        <ul className="clearlist widget-menu">
          {data.allMarkdownRemark.edges.map((edge) => (
            edge.node.frontmatter.blog_category.map((category) => {
              const c = category.replace(/_/g, ' ').charAt(0).toUpperCase() + category.slice(1);
              if (!categories.includes(category)) {
                categories.push(category);
                const count = data.allMarkdownRemark.edges.filter((edge) => edge.node.frontmatter.blog_category.includes(category)).length;
                return (
                  <li key={category}>
                    <a href={`/blog/category/${category}`} title="">{c}</a>
                    <small key={count}>- {count}</small>
                  </li>
                );
              }
            })
          ))}
        </ul>
      </div>
    </div>
  );
};

export default Categories;