import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

const Tags = () => {
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark(filter: {frontmatter: {blueprint: {eq: "blog"}}}) {
        edges {
          node {
            frontmatter {
              blog_tags
            }
          }
        }
      }
    }
  `);

  let tags = [];


 

  return (
    <div className="widget">
      <h3 className="widget-title">Tags</h3>
      <div className="widget-body">
        <div className='tags'>
       
          {data.allMarkdownRemark.edges.map((edge) => (
            edge.node.frontmatter.blog_tags.map((tag) => {
              const c = tag.replace(/_/g, ' ').charAt(0).toUpperCase() + tag.slice(1);
              if (!tags.includes(tag)) {
                tags.push(tag);
                const count = data.allMarkdownRemark.edges.filter((edge) => edge.node.frontmatter.blog_tags.includes(tag)).length;
                return (
                 
                    <a key={tag} href={`/blog/tag/${tag}`} title="">{c} ({count})</a>
                
                );
              }
            })
          ))}
       </div>
      </div>
    </div>
  );
};

export default Tags;