import React from 'react';
import { useLocation } from '@reach/router';
import { Image } from 'gatsby-plugin-image';
import { useStaticQuery, graphql } from 'gatsby';



export default function Services({ passedData }) {
  const data = useStaticQuery(graphql`
    query ServicePages {
        allMarkdownRemark(filter: {frontmatter: {blueprint: {eq: "service"}}}, limit: 10) {
        edges {
          node {
            frontmatter {
              title
              api_handle
              service_image
            }
          }
        }
      }
    }
  `);

 

  const services = data.allMarkdownRemark.edges;
  
  
  const location = useLocation();
  

  return (
    <>
      {location.pathname === '/' ? (
        
        <section className="page-section" id="services">
        <div className="container">
            
            <div className="mb-100 mb-md-40">
                <div className="row">                                
                    <div className="col-sm-6">
                        <h2 className="section-caption mb-0 mb-xs-10 black">{passedData.title}</h2>
                    </div>                                
                    <div className="col-sm-6 local-scroll mt-n10 mb-n10 text-sm-end">
                        <a href={passedData.button_link} className="link-hover-anim align-middle" data-link-animate="y">{passedData.button_text}<i className="icon-arrow-right2 size-14"></i></a>
                    </div>                                
                </div>
                <hr className="black mt-3 mb-0 " />
            </div>
            
           
            <div className="mb-100 mb-md-40">
            {services && services.map((item, index) => (
  <React.Fragment key={item.node.frontmatter.api_handle}>
    {item.node.frontmatter && index < 4 && (
      <h3
        className="services-4-item text-center parallax-mousemove-scene wow fadeInUp"
      >
        <a href={`/services/${item.node.frontmatter.api_handle}`}>
          <span className="services-4-title hs-title-5 font-alt overflow-hidden">
            {item.node.frontmatter.title}
            <b>{index + 1}</b>
          </span>
          <span className="services-4-image parallax-mousemove-follow">
            <img
              src={`/assets/${item.node.frontmatter.service_image}`}
              loading="lazy"
              width="550"
              height="673"
              alt={item.node.frontmatter.title}
            />
          </span>
        </a>
      </h3>
    )}
  </React.Fragment>
))}

                
                
            </div>
           
            
            <div className="row">
                <div className="col-md-10 offset-md-1 col-lg-8 offset-lg-2">
                    <hr className="black mt-0 mb-20 " />
                    <div className="row">
                        <div className="col-sm-6 col-md-7 text-center text-sm-start mb-xs-20">
                            <p className="section-descr mb-0 black">{passedData.short_description}</p>
                        </div>
                        <div className="col-sm-6 col-md-5 text-center text-sm-end local-scroll">
                            <a href={passedData.button_link_1} className="btn btn-mod btn-border btn-medium btn-circle" data-btn-animate="y">{passedData.button_text_1}</a>
                        </div>
                    </div>
                </div>
            </div>
            
        </div>
          </section>
    

      ) : (
        <section className="page-section" id="services">
        <div className="container">
            
            <div className="row mb-100 mb-md-60">
                
                <div className="col-lg-8 mb-md-50">
                    <div className="section-descr-extralarge wow linesAnimIn" data-wow-offset="0" data-splitting="lines">
                        <h2 className="section-title-inline-1">{passedData.title}</h2> 
                                {passedData.description}                               
                    </div>
                </div>
                
                <div className="col-lg-4 wow fadeInUp" data-offset="0">
                    <img src={`/assets/${passedData.image}`} alt="{passedData.title}" />
                </div>
                
            </div>
            
            <hr className="black mt-0 mb-80 mb-md-50 " />
            
         
            <div className="mb-100 mb-md-40">
                
                
                {services && services.map((item, index) => (
                    <React.Fragment key={index}>
                      
                      <h3 
                        className="services-4-item text-center parallax-mousemove-scene wow fadeInUp">
                          <a href={`/services/${item.node.frontmatter.api_handle}`}>
                        <span className="services-4-title hs-title-5 font-alt overflow-hidden">
                          {item.node.frontmatter.title} <b>{index + 1}</b>
                        </span>
                        <span className="services-4-image parallax-mousemove-follow">
                          <img 
                            src={`/assets/${item.node.frontmatter.service_image}`} 
                            loading="lazy" 
                            width="550" 
                            height="673" 
                            alt="{item.node.frontmatter.title}" 
                          />
                       
                        </span>
                        </a>
                      </h3>
                    </React.Fragment>
                  ))}
                
                
            </div>
            
            
            <div className="row">
                <div className="col-md-10 offset-md-1 col-lg-8 offset-lg-2">
                    <hr className="black mt-0 mb-20 " />
                    <div className="row">
                        <div className="col-sm-6 col-md-7 text-center text-sm-start mb-xs-20">
                            <p className="section-descr mb-0 black">
                                {passedData.short_description}
                            </p>
                        </div>
                        <div className="col-sm-6 col-md-5 text-center text-sm-end local-scroll">
                            <a href="{passedData.button_link_1}" className="btn btn-mod btn-border btn-medium btn-circle" data-btn-animate="y">{passedData.button_text_1}</a>
                        </div>
                    </div>
                </div>
            </div>
            
        </div>
    </section>




     )}
    </>
  );

  
}

