import React from 'react'
import { useStaticQuery, graphql } from 'gatsby';
import { useLocation } from '@reach/router';

export default function Portfolio({passedData}) {

    const data = useStaticQuery(graphql`
       query Portfolio {
  allMarkdownRemark(filter: {frontmatter: {blueprint: {eq: "portfolio"}}}) {
    edges {
      node {
        frontmatter {
          portfolio_items {
            image
            title
            title_2
          }
          title
          api_handle
        }
      }
    }
  }
}
  `);


  const portfolio = data.allMarkdownRemark.edges;
  
  const location = useLocation();
  return (
     
  <section className="page-section" id="portfolio">
  <div className="container">

  {location.pathname === '/' && (
  <div className="mb-100 mb-md-40">
                            <div className="row">                                
                                <div className="col-sm-6">
                                    <h2 className="section-caption mb-0 mb-xs-10 black">{passedData.title}</h2>
                                </div>                                
                                <div className="col-sm-6 local-scroll mt-n10 mb-n10 text-sm-end">
                                    <a href={passedData.link} className="link-hover-anim align-middle" data-link-animate="y"><span className="link-strong link-strong-unhovered">{passedData.button_text} <i className="icon-arrow-right2 size-14" aria-hidden="true"></i></span><span className="link-strong link-strong-hovered" aria-hidden="true">See all projects <i className="icon-arrow-right2 size-14" aria-hidden="true"></i></span></a>
                                </div>                                
                            </div>
                            <hr className="black mt-3 mb-0 " />
                        </div>
  )}
      
      <div className="row mb-md-40">

        
          
          <div className="col-lg-5"> 
                                         
              
              
              <h3 className="section-title mb-0"><span className="wow charsAnimIn" data-splitting="chars">{passedData.sub_title}</span></h3>                                
          
          </div>                            
          
          <div className="col-lg-7">
              
                               
              <div className="works-filter text-md-left text-lg-end mt-50 mt-md-30">
                  <a href="#" className="filter active" role="button" aria-pressed="true" data-filter="*">All Works</a>
                  
                  
{portfolio && portfolio.map((item, index) => (
  <React.Fragment key={item.node.frontmatter.api_handle}>
    {item.node.frontmatter && (
      <a
        href={`#${item.node.frontmatter.api_handle}`}
        className="filter"
        role="button"
        aria-pressed="false"
        data-filter={`.${item.node.frontmatter.api_handle}`}
      >
        {item.node.frontmatter.title}
      </a>
    )}
  </React.Fragment>
))}





              </div>
                
                                           
          </div>
          
      </div>                       
      
      
      <ul className="works-grid work-grid-3 work-grid-gut-lg masonry" id="work-grid">
  {portfolio && portfolio.map((parentItem, parentIndex) => (
    <React.Fragment key={parentItem.node.frontmatter.api_handle}>
      {parentItem.node.frontmatter.portfolio_items &&
        parentItem.node.frontmatter.portfolio_items.map((item, index) => (
          (index < 3 || location.pathname != '/') && (
            <li
              className={`work-item mt-90 mt-md-0 mix ${parentItem.node.frontmatter.api_handle}`}
              key={index}
            >
              <a
                href={`/assets/${item.image}`}
                className="work-lightbox-link mfp-image"
              >
                <div className="work-img">
                  <div className="work-img-bg wow-p scalexIn"></div>
                  <img
                    src={`/assets/${item.image}`}
                    alt={item.title}
                    className="wow-p fadeIn"
                    data-wow-delay="1s"
                  />
                </div>
                <div className="work-intro text-start">
                  <h3 className="work-title">{item.title}</h3>
                  <div className="work-descr">{item.title_2}</div>
                </div>
              </a>
            </li>
          )
        ))}
    </React.Fragment>
  ))}
</ul>
     
      
  </div>
</section>

  )
}
