import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { useLocation } from '@reach/router';
import moment from 'moment';

const TagBlogs = ({ passedData }) => {
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark(
        filter: { frontmatter: { blueprint: { eq: "blog" } } }
        sort: { fields: frontmatter___date_created, order: ASC }
      ) {
        edges {
          node {
            frontmatter {
              api_handle
              title
              image
              date_created
              blog_tags
            }
          }
        }
      }
    }
  `);

  
  const location = useLocation();
  const tag = location.pathname.replace(new RegExp('/blog/tag/', 'g'), "").replace(new RegExp('/', 'g'), "");

  const blogs = data.allMarkdownRemark.edges.filter((edge) => edge.node.frontmatter.blog_tags.includes(tag));
  const c = tag.replace(/_/g, ' ').charAt(0).toUpperCase() + tag.slice(1);
  return (
    <section 
    className={`page-section pb-0 pt-0 ${location.pathname !== '/' ? 'mt-100' : ''}`}
    id="blog"
  >
    <div className="container">
        
        <div className="mb-100 mb-md-40">
            <div className="row">                                
                <div className="col-sm-6">
                    <h2 className="section-caption mb-0 mb-xs-10 black">{passedData.title_1} - Tag - {c}</h2>
                </div>                                
                <div className="col-sm-6 local-scroll mt-n10 mb-n10 text-sm-end">
                    <a href={passedData.button_link} className="link-hover-anim align-middle" data-link-animate="y">
                      <span className="link-strong link-strong-unhovered">All news <i className="icon-arrow-right2 size-14" aria-hidden="true"></i></span>
                      <span className="link-strong link-strong-hovered" aria-hidden="true">{passedData.button_text}<i className="icon-arrow-right2 size-14" aria-hidden="true"></i></span>
                      </a>
                </div>                                
            </div>
            <hr className="black mt-3 mb-0 " />
        </div>
        
        <div className="row">
            <div className="col-8 col-sm-7">
              


{/* <h3 className="hs-title-5 font-alt overflow-hidden mb-100 mb-md-40">
  <span className="d-block wow fadeRotateIn" data-wow-delay="0.2s">
  {passedData.title_2}
  </span>
  <span className="d-block text-end wow fadeRotateIn" data-wow-delay="0.4s">
  {passedData.title_3}
  </span>
</h3> */}


            </div>
        </div>
        
        <div className="row gy-4">
            
        {blogs && blogs.map((item, index) => (
  
    <React.Fragment key={item.api_handle}>
      {item && (
        <div key={index}
          className="post-prev-2 col-md-6 col-lg-4"
          data-rellax-y
          data-rellax-speed="-1"
          data-rellax-percentage="0.37"
        >
          <div className="mt-md-0">
            <div className="post-prev-2-img">
              <a href={`/blog/${item.node.frontmatter.api_handle}`}>
                <img
                  src={`/assets/${item.node.frontmatter.image}`}
                  alt={item.node.frontmatter.title}
                />
              </a>
            </div>
            <h3 className="post-prev-2-title">
              <a href={`/blog/${item.node.frontmatter.api_handle}`}>
                {item.node.frontmatter.title}
              </a>
            </h3>
            <div className="post-prev-2-info">
            {moment(item.node.frontmatter.date_created).format('MMMM D, YYYY')}
            </div>
          </div>
        </div>
      )}
    </React.Fragment>
  
))}

            
</div>
        
     



        
       
    
    </div>
    </section>
  );
};

export default TagBlogs;