import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import moment from "moment"

const LatestPosts = ({ passedData }) => {
  const blogs = useStaticQuery(graphql`
    query {
      allMarkdownRemark(
        filter: { frontmatter: { blueprint: { eq: "blog" } } }
        limit: 5
        sort: { frontmatter: { date_created: ASC } }
      ) {
        edges {
          node {
            frontmatter {
              api_handle
              image
              date_created
              title
            }
          }
        }
      }
    }
  `)

 

  return (
    <div className="widget">
      <h3 className="widget-title">Latest posts</h3>

      <div className="widget-body">
        <ul className="clearlist widget-posts">
          {blogs &&
            blogs.allMarkdownRemark.edges.map((item, index) => (
              <React.Fragment key={item.api_handle}>
                {item && (
                 
                    <li className="clearfix">
                      <a
                        href={`/blog/${item.node.frontmatter.api_handle}`}
                        title={item.node.frontmatter.title}
                      >
                        <img
                          src={`/assets/${item.node.frontmatter.image}`}
                          alt={item.node.frontmatter.title}
                          width="100"
                          className="widget-posts-img"
                        />
                      </a>
                      <div className="widget-posts-descr">
                        <a
                          href={`/blog/${item.node.frontmatter.api_handle}`}
                          title={item.node.frontmatter.title}
                        >
                          {item.node.frontmatter.title}
                        </a>
                        {moment(item.node.frontmatter.date_created).format(
                          "MMMM D, YYYY"
                        )}
                      </div>
                    </li>
                  
                )}
              </React.Fragment>
            ))}
        </ul>
      </div>
    </div>
  )
}

export default LatestPosts